
import './detalleMobil.css'
import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router";
import { Carousel } from 'react-bootstrap';
import {url} from '../URL.js'
import { subespecimen } from '../store/action';

export function DetalleMobil(){
    let id = useParams()
    let ids = id.id
    const [especimen, setEspecimen] = useState(null)
     useEffect(() => {
        axios.get(`${url}especimen/id?id=${ids}`)
        .then((response) => {
            setEspecimen(response.data) 
        })
        return () => {
            setEspecimen(null)
        }
    }, []) 

    return (
        <div class='padre432'>
            <div class='hijo1'> 
                <label class='label-type'>Especimen Nro:</label>
                <p>{ especimen?.especimennumero ? subespecimen(especimen?.especimennumero.toString()): ''}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Genero:</label>
                <p>{especimen?.genero}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Especie:</label>
                <p>{especimen?.especie}</p>
            </div>
            <div class='hijo2'> 
                <label class='label-type'>Posicion Filogenetica:</label>
                <ul>
                {especimen?.posicionfilo.map(e=>{
                    return <li class='datos'>{e}</li>
                })}
                </ul>
            </div>
            <div class='hijo2'> 
                <label class='label-type'>Partes Esqueletales:</label>
                <ul>
                {especimen?.partesesqueletales.map(e=>{
                    return <li class='datos'>{e}</li>
                })}
                </ul>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Periodo:</label>
                <p>{especimen?.periodo}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Epoca:</label>
                <p>{especimen?.epoca}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Piso:</label>
                <p>{especimen?.piso}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Cuenca:</label>
                <p>{especimen?.cuenca}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Formacion:</label>
                <p>{especimen?.formacion}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Miembro:</label>
                <p>{especimen?.miembro}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Localidad:</label>
                <p>{especimen?.localidad}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Cord Lat:</label>
                <p>{especimen?.coordlat}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Cord Long:</label>
                <p>{especimen?.coordlong}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Campaña:</label>
                <p>{especimen?.camapana}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Descubridor:</label>
                <p>{especimen?.descubridor}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Fecha Descubrimiento:</label>
                <p>{especimen?.fechadescubrimiento}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Nro Campo:</label>
                <p>{especimen?.nrocampo}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Preparador::</label>
                <p>{especimen?.preparador}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Fecha Preparacion:</label>
                <p>{especimen?.preparacionfecha}</p>
            </div>
            <div class='hijo2'> 
                <label class='label-type'>Ubicacion Fosil/es:</label>
                <p>Armario 1 : {especimen?.armario1} desde: {especimen?.estante1desde} hasta: {especimen?.estante1hasta}</p>
                <p>Armario 2 : {especimen?.armario2} desde: {especimen?.estante2desde} hasta: {especimen?.estante2hasta}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Cantidad de Fragmentos:</label>
                <p>{especimen?.cantidadfrag}</p>
            </div>
            <div class='hijo1'> 
                <label class='label-type'>Comentario:</label>
                <p>{especimen?.comentario}</p>
            </div>
            <div class='hijo1'> 
            <div class='carrusel'>
                 {
                    especimen?.imagen?.length>0?
                    <Carousel variant= 'dark'>
                    {especimen?.imagen?.map(el => { 
                        return <Carousel.Item>
                        <div class='div-imagen'>
                        <img 
                                src={el}
                                alt='banner'
                                class='carrusel-item-css'
                        />
                        </div>
                        </Carousel.Item>
                    })}
                    
                  
                    </Carousel>
                        :<>
                        <img src='https://plantillasdememes.com/img/plantillas/imagen-no-disponible01601774755.jpg'
                        width= "150px"
                        height= "150px"
                        />
                        
                        </>
                    }
                    </div>
            </div>
            
        </div>
    )

}