            dispatch(getFilo())
            dispatch(getPartes())
            axios.get(`${url}bochon/especimen/?parametro=nuevo`)
            .then((response) => {
                setNewIdBochon(response.data)
               
            })
             axios.get(`${url}especimen/?parametro=nuevoEsp`)
